body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  display: none;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Roboto', system-ui, -apple-system, 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background: radial-gradient(circle at 10% 10%, #111 0%, #0d0d0d 30%), linear-gradient(180deg, #0b0b0b, #1a1a1a);
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #e6e6e6;
  background-attachment: fixed;
}

.container {
  width: 100%;
  max-width: 900px;
  margin: 0 auto;
  padding: 20px;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

/* Header and Title Styling */
.header {
  text-align: center;
  margin-bottom: 40px;
  animation: slideDown 0.8s ease-out;
}

.title-wrapper {
  background: linear-gradient(180deg, rgba(255,255,255,0.03), rgba(255,255,255,0.02));
  padding: 30px 24px;
  border-radius: 12px;
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.6);
  border: 1px solid rgba(255,255,255,0.03);
}

.main-title {
  font-size: 44px;
  font-weight: 700;
  margin-bottom: 8px;
  letter-spacing: 1px;
  color: #fff;
  font-family: 'Oswald', sans-serif;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  text-transform: uppercase;
  text-shadow: 0 4px 18px rgba(0,0,0,0.6);
}

.main-title span {
  display: inline-block;
  margin: 0 6px;
}

.main-title .flag {
  font-size: 30px;
  margin-right: 6px;
  transform: translateY(2px);
}

.main-title .delta,
.main-title .dash,
.main-title .online,
.main-title .assistant {
  color: #fff;
  background: linear-gradient(90deg, #e53935 0%, #ffb300 60%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.subtitle {
  font-size: 14px;
  color: #d1d1d1;
  font-weight: 400;
  letter-spacing: 1px;
  margin-top: -4px;
}

/* Navigation Menu */
.navigation {
  display: flex;
  gap: 10px;
  margin-bottom: 25px;
  flex-wrap: wrap;
  justify-content: center;
  animation: slideDown 0.8s ease-out 0.1s both;
}

.nav-btn {
  padding: 10px 18px;
  background: rgba(255,255,255,0.03);
  color: #fff;
  border: 1px solid rgba(255,255,255,0.04);
  border-radius: 8px;
  font-size: 13px;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.18s ease;
  display: flex;
  align-items: center;
  gap: 8px;
  text-transform: uppercase;
  letter-spacing: 0.6px;
}

.nav-btn:hover {
  background: white;
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

.nav-btn.active {
  background: linear-gradient(90deg, #e53935 0%, #ffb300 60%);
  color: #111;
  border-color: rgba(255,255,255,0.06);
}

.nav-btn .icon {
  font-size: 16px;
}

/* Main Content */
.main-content {
  flex: 1;
  background: linear-gradient(180deg, rgba(255,255,255,0.02), rgba(255,255,255,0.01));
  border-radius: 12px;
  padding: 28px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.6);
  display: flex;
  flex-direction: column;
  animation: slideUp 0.8s ease-out 0.2s both;
  position: relative;
  border: 1px solid rgba(255,255,255,0.03);
}

/* Page Content */
.page-content {
  display: none;
  flex: 1;
  animation: fadeIn 0.4s ease-out;
}

.page-content.active {
  display: flex;
  flex-direction: column;
}

.page-content#chat-page {
  flex-direction: column;
}

.content-box {
  padding: 20px 0;
  overflow-y: auto;
}

.content-box h2 {
  color: #ffb300;
  margin-bottom: 20px;
  font-size: 26px;
}

.content-box h3 {
  color: #e53935;
  margin-top: 20px;
  margin-bottom: 15px;
  font-size: 18px;
}

.content-box h4 {
  color: #667eea;
  margin-bottom: 8px;
}

.content-box p {
  color: #555;
  line-height: 1.6;
  margin-bottom: 15px;
}

.content-box ul {
  list-style: none;
  padding-left: 0;
  margin-bottom: 20px;
}

.content-box ul li {
  color: #555;
  padding: 8px 0;
  padding-left: 25px;
  position: relative;
}

.content-box ul li:before {
  content: "üèÅ";
  position: absolute;
  left: 0;
  color: #ffb300;
}

/* Input Section */
.input-section {
  margin-bottom: 30px;
}

.input-wrapper {
  display: flex;
  gap: 10px;
  align-items: center;
}

.input-field {
  flex: 1;
  padding: 15px 20px;
  border: 2px solid #e0e0e0;
  border-radius: 12px;
  font-size: 16px;
  transition: all 0.3s ease;
  font-family: inherit;
}

.input-field:focus {
  outline: none;
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

.input-field::placeholder {
  color: #999;
}

.submit-btn {
  padding: 15px 35px;
  background: linear-gradient(135deg, #667eea, #764ba2);
  color: white;
  border: none;
  border-radius: 12px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.submit-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 25px rgba(102, 126, 234, 0.4);
}

.submit-btn:active {
  transform: translateY(0);
}

/* Response Section */
.response-section {
  flex: 1;
  overflow-y: auto;
  padding-right: 10px;
  margin-bottom: 20px;
}

.response-container {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.welcome-message {
  color: #999;
  font-style: italic;
  text-align: center;
  padding: 40px 20px;
}

.response-message {
  padding: 15px 20px;
  border-radius: 12px;
  animation: fadeIn 0.3s ease-out;
  word-wrap: break-word;
}

.response-message.user {
  background: linear-gradient(135deg, #667eea, #764ba2);
  color: white;
  margin-left: auto;
  max-width: 70%;
  text-align: right;
}

.response-message.assistant {
  background: #f5f5f5;
  color: #333;
  margin-right: auto;
  max-width: 70%;
  border-left: 4px solid #667eea;
}

/* Footer */
.footer {
  text-align: center;
  padding: 20px;
  color: rgba(255, 255, 255, 0.8);
  font-size: 14px;
}

.footer-link {
  color: rgba(255, 255, 255, 0.9);
  text-decoration: none;
  transition: all 0.3s ease;
  font-weight: 600;
}

.footer-link:hover {
  color: white;
  text-decoration: underline;
}

/* Animations */
@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes float {
  0%, 100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-5px);
  }
}

/* Scrollbar Styling */
.response-section::-webkit-scrollbar {
  width: 8px;
}

.response-section::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 10px;
}

.response-section::-webkit-scrollbar-thumb {
  background: #667eea;
  border-radius: 10px;
}

.response-section::-webkit-scrollbar-thumb:hover {
  background: #764ba2;
}

/* Form Styling */
.form-group {
  margin-bottom: 20px;
}

.form-group label {
  display: block;
  margin-bottom: 8px;
  color: #667eea;
  font-weight: 600;
  font-size: 14px;
}

.form-input,
.form-textarea {
  width: 100%;
  padding: 12px 15px;
  border: 2px solid #e0e0e0;
  border-radius: 8px;
  font-size: 14px;
  font-family: inherit;
  transition: all 0.3s ease;
  color: #333;
}

.form-input:focus,
.form-textarea:focus {
  outline: none;
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

.form-textarea {
  resize: vertical;
  min-height: 120px;
}

/* Strategy Calculator */
.strategy-form {
  background: #f9f9f9;
  padding: 25px;
  border-radius: 12px;
  margin: 20px 0;
  border-left: 4px solid #667eea;
}

.calculate-btn {
  padding: 15px 30px;
  background: linear-gradient(135deg, #667eea, #764ba2);
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  width: 100%;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.calculate-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 25px rgba(102, 126, 234, 0.4);
}

.strategy-result {
  background: #f0f4ff;
  border: 2px solid #667eea;
  border-radius: 12px;
  padding: 20px;
  margin-top: 20px;
  min-height: 100px;
  color: #333;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.strategy-result.active {
  background: linear-gradient(135deg, #f0f4ff, #f5f1ff);
}

/* Version Info */
.version-info {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
  margin: 25px 0;
}

.version-item {
  background: linear-gradient(135deg, #667eea, #764ba2);
  color: white;
  padding: 25px;
  border-radius: 12px;
  text-align: center;
  box-shadow: 0 5px 15px rgba(102, 126, 234, 0.3);
}

.version-item h3 {
  color: white;
  margin-top: 0;
  font-size: 14px;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-bottom: 10px;
}

.version-number,
.version-date,
.version-status {
  font-size: 24px;
  font-weight: 700;
  margin: 0;
}

.version-history {
  margin: 20px 0;
}

.history-item {
  background: #f9f9f9;
  padding: 20px;
  border-radius: 8px;
  border-left: 4px solid #764ba2;
  margin-bottom: 15px;
}

.history-item h4 {
  margin-top: 0;
}

.history-item p {
  margin-bottom: 0;
  color: #666;
}

.check-update-btn {
  padding: 12px 25px;
  background: #667eea;
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: 15px;
}

.check-update-btn:hover {
  background: #764ba2;
  transform: translateY(-2px);
}

/* Feedback Form */
.feedback-form {
  background: #f9f9f9;
  padding: 25px;
  border-radius: 12px;
  margin: 20px 0;
}

.submit-feedback-btn {
  padding: 15px 30px;
  background: linear-gradient(135deg, #667eea, #764ba2);
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 1px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
}

.submit-feedback-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 25px rgba(102, 126, 234, 0.4);
}

/* Button Group */
.button-group {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 15px;
  margin-top: 20px;
}

.submit-feedback-btn.local-btn {
  background: linear-gradient(135deg, #667eea, #764ba2);
}

.submit-feedback-btn.online-btn {
  background: linear-gradient(135deg, #764ba2, #f093fb);
}

.btn-icon {
  font-size: 18px;
}

.feedback-status {
  margin-top: 15px;
  padding: 15px;
  border-radius: 8px;
  text-align: center;
  font-weight: 600;
  display: none;
}

.feedback-status.success {
  display: block;
  background: #d4edda;
  border: 2px solid #28a745;
  color: #155724;
}

.feedback-status.error {
  display: block;
  background: #f8d7da;
  border: 2px solid #dc3545;
  color: #721c24;
}

/* Email Service Modal */
.email-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  animation: fadeIn 0.3s ease-out;
}

.modal-content {
  background: white;
  border-radius: 15px;
  box-shadow: 0 10px 50px rgba(0, 0, 0, 0.3);
  max-width: 500px;
  width: 90%;
  overflow: hidden;
  animation: slideUp 0.3s ease-out;
}

.modal-header {
  background: linear-gradient(135deg, #667eea, #764ba2);
  color: white;
  padding: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.modal-header h3 {
  margin: 0;
  font-size: 20px;
}

.modal-close {
  background: none;
  border: none;
  color: white;
  font-size: 28px;
  cursor: pointer;
  transition: all 0.2s ease;
  padding: 0;
  line-height: 1;
}

.modal-close:hover {
  transform: rotate(90deg);
}

.modal-body {
  padding: 25px;
}

.modal-body p {
  margin-bottom: 20px;
  color: #666;
  text-align: center;
}

.email-options {
  display: grid;
  grid-template-columns: 1fr;
  gap: 12px;
}

.email-option {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 12px;
  padding: 15px 20px;
  border-radius: 10px;
  text-decoration: none;
  font-weight: 600;
  transition: all 0.3s ease;
  color: white;
  border: 2px solid transparent;
}

.gmail-btn {
  background: linear-gradient(135deg, #EA4335, #C5221F);
}

.gmail-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(234, 67, 53, 0.4);
}

.outlook-btn {
  background: linear-gradient(135deg, #0078D4, #004B87);
}

.outlook-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(0, 120, 212, 0.4);
}

.yahoo-btn {
  background: linear-gradient(135deg, #7C2AE8, #510FA9);
}

.yahoo-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(124, 42, 232, 0.4);
}

.service-icon {
  font-size: 20px;
}

.service-name {
  font-size: 16px;
}
@media (max-width: 768px) {
  .main-title {
    font-size: 32px;
  }

  .main-title span {
    display: block;
    margin: 5px 0;
  }

  .title-wrapper {
    padding: 30px 20px;
  }

  .main-content {
    padding: 25px;
  }

  .response-message.user,
  .response-message.assistant {
    max-width: 100%;
  }

  .input-field,
  .submit-btn {
    padding: 12px 15px;
    font-size: 14px;
  }

  .navigation {
    gap: 8px;
  }

  .nav-btn {
    padding: 10px 15px;
    font-size: 12px;
  }

  .content-box h2 {
    font-size: 22px;
  }

  .form-group {
    margin-bottom: 15px;
  }
}
